@import "../../variables.scss";

.cardBack {
  padding: 0px 20px;
  
  .nameABV {
  max-height: 38px;
  overflow: hidden;

    span {
      color: $blue;
      font-size: $h3-size !important;
      font-weight: 400!important;
    }
  }

  .firstBrewed {
    display: flex;
    align-items: center;
    padding: 5px 0;

    p {
      color: $blue;
      padding-left: 5px;
      text-transform: uppercase;
      font-weight: 400!important;
      font-size: $h4-size!important;
    }
  }

  .description {
    padding: 5px 0;
  }
    .descriptionText {
      max-height: 115px;
      overflow: scroll;
      overflow-x: hidden;
      overflow-y: auto;

      &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px $grey;
        -webkit-box-shadow: inset 0 0 6px $grey;
        border-radius: 10px;
        background-color: #f5f5f5;
      }
    
      &::-webkit-scrollbar {
        width: 6px;
        background-color: #f5f5f5;
      }
    
      &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        box-shadow: inset 0 0 6px $grey;
        -webkit-box-shadow: inset 0 0 6px $grey;
        background-color: $grey;
      }
    }
    
    .ingredients {
      padding: 5px 0;

    .malt, .hops, .yeast {
      display: flex;
      padding: 5px 0;
      max-height: 54px;
      overflow: hidden;

      p {
        color: $grey;
        padding: 2px 0px 0px 5px;
      }
    }
  }

  .buttonBack {
    padding-top: 10px;
    position: absolute;
    bottom: 10px;
  }


}